@charset "utf-8";

.kolonsport-site {
  .point-detail {
    padding: 0 6.66vw;
    h3 {
      padding: 6vw 0 2.666vw;
      font-size: 4.4vw;
      font-weight: 900;
      color: #1f1f1f;
      border-bottom: .4vw solid #1f1f1f;
    }
    .detail-txt {
      padding-top: 4.4vw;
      font-size: 4vw;
      color: #1f1f1f;
      word-break: keep-all;
      letter-spacing: -.5px;
    }
    .saved-point {
      border-bottom: 1px solid #1f1f1f;
      dl {
        padding: 2.666vw 0;
        border-top: 1px solid #f1f1f1;
        dt,
        dd {
          display: inline-block;
          font-size: 3.2vw;
          letter-spacing: -1px;
          vertical-align: middle;
        }
        dt {
          width: 30%;
          color: #1f1f1f;
        }
        dd {
          width: 66%;
          color: #89898c;
        }
        &.type {
          border: 0;
          dd {
            color: #1f1f1f;
          }
        }
      }
    }
    .information {
      margin: 6.4vw 0 12.666vw;
      ul {
        li {
          margin-bottom: 1.333vw;
          padding-left: 2vw;
          font-size: 3.2vw;
          color: #89898c;
          letter-spacing: -1.5px;
          word-break: keep-all;
          &::before {
            content: '·';
            display: inline-block;
            margin-left: -1.333vw;
            margin-right: .666vw;
            
          }
        }
      }
    }
  }
}
@media (min-width: 750px) {
  .kolonsport-site {
    .point-detail {
      padding: 0 50px;
      h3 {
        padding: 45px 0 20px;
        font-size: 33px;
        border-bottom: 3px solid #1f1f1f;
      }
      .detail-txt {
        padding-top: 33px;
        font-size: 30px;
      }
      .saved-point {
        dl {
          padding: 20px 0;
          dt,
          dd {
            font-size: 24px;
          }
        }
      }
      .information {
        margin: 48px 0 95px;
        ul {
          li {
            margin-bottom: 10px;
            padding-left: 15px;
            font-size: 24px;
            &::before {
            margin-left: -10px;
            margin-right: 5px;
            }
          }
        }
      }
    }
  }
}